import React, {useCallback, useMemo} from 'react'
import {useStoreActions, useStoreState} from 'easy-peasy'
import styled from 'styled-components'
import {MdSave} from 'react-icons/md'
import {noPrint} from 'app/styles'
import {Select} from 'app/components/Select'
import {IconButton} from 'app/components/IconButton'
import CompanyOption from 'app/components/Company/CompanyOption'
import {useCompanyModel} from 'app/components/Company/context'
import {newCompany} from 'app/components/Company/constants'

const Wrapper = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  ${noPrint}
`
const CompanySelect = styled(Select)`
  border: none;
  color: transparent;
  cursor: default;
  width: 140px;
`
const SaveButton = styled(IconButton)`
  padding: 4px 6px;
`

export default () => {
  const model = useCompanyModel()
  const setCompany = useStoreActions(actions => actions[model].set)
  const company = useStoreState(state => state[model].company, [model])
  const list = useStoreState(state => state[model].list, [model])
  const open = useStoreActions(actions => actions[model].openModal)

  const onChange = useCallback(value => {
    if (value === newCompany) {
      open()
    } else {
      setCompany(value)
    }
  })

  const options = useMemo(() => [
    {value: newCompany, label: 'Add company'},
    ...list.map(label => ({
      value: label,
      label
    }))
  ], [list])

  return (
    <Wrapper>
      {!!list.length ? (
        <CompanySelect
          value={company}
          options={options}
          Option={CompanyOption}
          onChange={onChange}
        />
      ) : (
        <SaveButton Icon={MdSave} onClick={open}/>
      )}
    </Wrapper>
  )
}
