import React, {useCallback, useMemo} from 'react'
import styled from 'styled-components'
import {useStoreActions, useStoreState} from 'easy-peasy'
import uuid from 'uuid'
import {Cell} from 'app/components/Grid'
import {Button} from 'app/components/Button'
import {downloadFile} from 'app/services/common'
import {buttonStyle, buttonTypes, buttonType} from 'app/components/Button/constants'
import {hiddenStyle} from 'app/styles'

const FileInput = styled.input`
  ${hiddenStyle}
`
const Label = styled.label`
  ${buttonStyle}
  ${buttonTypes[buttonType.outline]}
`

export default () => {
  const id = useMemo(uuid)
  const replaceState = useStoreActions(actions => actions.replace)
  // TODO check if it's good for performance
  const state = useStoreState(state => state)

  const download = useCallback(() => {
    downloadFile(JSON.stringify(state), 'invoice-data')
  })

  const handleChange = useCallback(e => {
    const fr = new FileReader()

    fr.onload = e => {
      replaceState(JSON.parse(e.target.result))
    }

    fr.readAsText(e.target.files[0])
  })

  return (
    <>
      <Cell middle>
        <Button onClick={download}>{'Export'}</Button>
      </Cell>
      <Cell middle>
        <FileInput id={id} type="file" onChange={handleChange}/>
        <Label htmlFor={id}>{'Import'}</Label>
      </Cell>
    </>
  )
}
