import React, {useRef, useState} from 'react'
import styled from 'styled-components'
import {InputNumber} from 'app/components/Input'
import {useFormattedNumber} from 'app/hooks'
import {hiddenStyle} from 'app/styles'

const Input = styled(InputNumber)`
  ${({ focused }) => !focused && hiddenStyle}
`

export const PriceInput = ({ className, value, onBlur, ...props }) => {
  const inputRef = useRef(null)
  const [focused, setFocused] = useState(false)
  const price = useFormattedNumber(value)

  const handleBlur = e => {
    setFocused(false)
    onBlur(e)
  }

  const handleFocus = () => {
    setFocused(true)
  }

  const focus = () => {
    inputRef.current.focus()
  }

  return (
    <>
      <Input
        {...props}
        inputRef={inputRef}
        className={className}
        focused={focused}
        value={value}
        onBlur={handleBlur}
        onFocus={handleFocus}
      />
      {!focused && value && (
        <div className={className} onClick={focus}>{price}</div>
      )}
    </>
  )
}
