import React from 'react'
import {useStoreState} from 'easy-peasy'
import {numberWithCommas} from 'app/services/common'

export const useFormattedNumber = (
  value,
  {
    formatter = numberWithCommas,
    precision = 2,
    currencySymbol,
  } = {}
) => {
  const symbol = useStoreState(state => state.currency.symbol)
  const [prefix, suffix] = currencySymbol || symbol
  const number = +value

  // TODO add useMemo
  return `${prefix}${formatter(number.toFixed(precision))} ${suffix}`
}
