import React from 'react'
import {useFormattedNumber} from 'app/hooks'
import {Field} from 'app/components/Field'
import {InputNumber, DashedInput} from 'app/components/Input'
import {PriceInput} from 'app/components/PriceInput'
import {Col, Row} from 'app/components/Items/Row'
import {getItemAmount} from 'app/components/Items/utils'
import AddButton from 'app/components/Items/AddButton'
import RemoveButton from 'app/components/Items/RemoveButton'

const Item = ({index, push, remove, item}) => {
  const amount = useFormattedNumber(getItemAmount(item))

  return (
    <Row columns={4}>
      <Col>
        <Field
          as={DashedInput}
          name={`items[${index}].item`}
        />
      </Col>
      <Col>
        <Field
          as={DashedInput}
          name={`items[${index}].quantity`}
          component={InputNumber}
          min={1}
          precision={0}
        />
      </Col>
      <Col>
        <Field
          as={DashedInput}
          name={`items[${index}].price`}
          component={PriceInput}
          min={0.01}
          precision={2}
          step={10}
        />
      </Col>
      <Col>{amount}</Col>
      <AddButton push={push} index={index}/>
      <RemoveButton remove={remove} index={index}/>
    </Row>
  )
}

export default ({push, remove, form: {values}}) =>
  values.items.map((item, index) =>
    <Item key={index} index={index} push={push} remove={remove} item={item}/>
  )
