import React, {useCallback, useMemo} from 'react'
import {useStoreActions, useStoreState} from 'easy-peasy'
import {MdAdd, MdRemove} from 'react-icons/md'
import {OptionIcon, OptionWithIcon} from 'app/components/Select/Option'
import {newCurrency} from './constants'

export default ({value, children, ...props}) => {
  const list = useStoreState(state => state.currency.list)
  const removeCurrency = useStoreActions(actions => actions.currency.remove)

  const remove = useCallback(e => {
    e.stopPropagation()
    removeCurrency(value)
  }, [value])

  const icon = useMemo(() => {
    if (value === newCurrency) {
      return (
        <OptionIcon
          Icon={MdAdd}
          title={'Add currency'}
        />
      )
    }

    if (list.length > 1) {
      return (
        <OptionIcon
          Icon={MdRemove}
          title={'Remove currency'}
          onClick={remove}
        />
      )
    }

    return null
  }, [value, list])

  return (
    <OptionWithIcon {...props}>
      {children}
      {icon}
    </OptionWithIcon>
  )
}
