export const numberWithCommas = (n) => n.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')

export const parser = (n) => n.toString().replace(/,*/g, '')

export function setManifest(properties) {
  const stringManifest = JSON.stringify({...MANIFEST, ...properties})
  const blob = new Blob([stringManifest], {type: 'application/json'})
  const manifestURL = URL.createObjectURL(blob)
  updateLink('manifest', manifestURL)
}

export function convertSvgToPng(svgString, width, height, callback) {
  // SVG data URL from SVG string
  const svgData = 'data:image/svg+xml;base64,' + btoa(unescape(encodeURIComponent(svgString)))
  // create canvas in memory(not in DOM)
  const canvas = document.createElement('canvas')
  // get canvas context for drawing on canvas
  const context = canvas.getContext('2d')
  // set canvas size
  canvas.width = width
  canvas.height = height
  // create image in memory(not in DOM)
  const image = new Image()
  // later when image loads run this
  image.onload = () => { // async (happens later)
    // clear canvas
    context.clearRect(0, 0, width, height)
    // draw image with SVG data to canvas
    context.drawImage(image, 0, 0, width, height)
    // snapshot canvas as png
    const pngData = canvas.toDataURL('image/png')
    // pass png data URL to callback
    callback(pngData)
  } // end async
  // start loading SVG data into in memory image
  image.src = svgData
}

