export const getBrowserDarkTheme = () => {
  const preferDarkQuery = '(prefers-color-scheme: dark)'
  const mql = global.matchMedia ? global.matchMedia(preferDarkQuery) : {}
  const isColorSchemeQuerySupported = mql.media === preferDarkQuery
  return isColorSchemeQuerySupported ? mql.matches : false
}

export const format = (date, formatStr) => {
  // to prevent appearing error related to 'awareOfUnicodeTokens'
  try {
    return fnsFormat(date, formatStr)
  } catch (e) {
    return 'Invalid Date'
  }
}

