import React, {useMemo} from 'react'
import {useStoreState} from 'easy-peasy'
import {Formik} from 'formik'
import {LABELS} from 'app/constants/common'
import {format} from 'app/services/common'
import {useAppContext} from 'app/components/Layout/context'
import Form from 'app/components/Details/Form'

export const Details = () => {
  const {query} = useAppContext()
  const dateFormat = useStoreState(state => state.settings.dateFormat)

  const initialValues = useMemo(() => ({
    details: [
      {label: LABELS.number, value: query.number || ''},
      {
        label: LABELS.date,
        value: query.date || (dateFormat ? format(new Date(), dateFormat) : '')
      },
    ]
  }), [query.number, query.date, dateFormat])

  return (
    <Formik
      component={Form}
      initialValues={initialValues}
    />
  )
}
