import React from 'react'
import styled from 'styled-components'
import {buttonType, buttonTypes, buttonStyle} from 'app/components/Button/constants'

const ButtonComponent = styled.button`
  ${buttonStyle}
  ${({ buttonType }) => buttonTypes[buttonType]}
`

export const Button = ({style, className, children, onClick, type = buttonType.outline, htmlType = 'button'}) => (
  <ButtonComponent
    type={htmlType}
    style={style}
    className={className}
    onClick={onClick}
    buttonType={type}
  >
    {children}
  </ButtonComponent>
)

Button.type = buttonType
