import React, {useMemo} from 'react'
import styled from 'styled-components'
import {Formik} from 'formik'
import {LABELS} from 'app/constants/common'
import {useAppContext} from 'app/components/Layout/context'
import {Field} from 'app/components/Field'
import {LabelInput} from 'app/components/Input'

const Input = styled(LabelInput)`
  font-size: 24px;
  text-align: right;
`

const Form = () => (
  <Field
    as={Input}
    name='title'
  />
)

export const Title = () => {
  const {query} = useAppContext()

  const initialValues = useMemo(() => ({
    title: query.title || LABELS.title
  }), [query.title])

  return (
    <Formik
      component={Form}
      initialValues={initialValues}
    />
  )
}
