import React from 'react'
import {useStoreActions, useStoreState} from 'easy-peasy'
import {Modal} from 'app/components/Modal'
import CurrencyForm from 'app/components/CurrencyModal/CurrencyForm'

export default ({submitForm}) => {
  const close = useStoreActions(actions => actions.currency.closeModal)
  const opened = useStoreState(state => state.currency.modalOpened)

  return (
    <Modal
      title={'Add Currency'}
      opened={opened}
      onCancel={close}
      onOk={submitForm}
      width={400}
    >
      <CurrencyForm/>
    </Modal>
  )
}
