import React, {useMemo} from 'react'
import uuid from 'uuid'
import styled from 'styled-components'
import {colors, hiddenStyle} from 'app/styles'

// TODO replace '#fff' with baseColors.white
const Label = styled.label`
  position: relative;
  display: inline-block;
  width: 44px;
  height: 22px;
  padding: 0;
  vertical-align: middle
  border-radius: 20px;
  border: 1px solid ${colors.switch};
  background-color: ${colors.switch};
  cursor: pointer;
  
  &:after {
    position: absolute;
    width: 18px;
    height: 18px;
    left: 2px;
    top: 1px;
    border-radius: 50%;
    background-color: ${'#fff'};
    content: " ";
    cursor: pointer;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.26);
    transition: left .3s cubic-bezier(0.35, 0, 0.25, 1);
  }
  
  ${({ checked }) => checked && `
    border: 1px solid ${colors.primary};
    background-color: ${colors.primary};
  
    &:after {
      left: 22px;
    }
  `}
`

const Checkbox = styled.input`
  ${hiddenStyle}
`

export const Switch = ({style, className, ...props}) => {
  const id = useMemo(uuid)

  return (
    <>
      <Label
        htmlFor={id}
        style={style}
        checked={!!props.value}
        className={className}
      />
      <Checkbox id={id} type="checkbox" checked={props.value} {...props} />
    </>
  )
}
