import React from 'react'
import {useStoreActions, useStoreState} from 'easy-peasy'
import {ColorPicker} from 'app/components/ColorPicker'
import Row from './Row'

export default () => {
  const setPrimaryColor = useStoreActions(actions => actions.settings.setPrimaryColor)
  const primaryColor = useStoreState(state => state.settings.primaryColor)

  return (
    <Row label={'Primary color'}>
      <ColorPicker
        value={primaryColor}
        onChange={setPrimaryColor}
      />
    </Row>
  )
}
