import {head, keys, prop} from 'ramda'
import {action, computed} from 'easy-peasy'
import {DEFAULT_CURRENCIES} from 'app/constants/common'
import {createCurrencySymbol} from 'app/services/common'

export default {
  currency: 'USD',
  currencies: DEFAULT_CURRENCIES,
  list: computed(state => keys(state.currencies)),
  symbol: computed(state => prop(state.currency, state.currencies)),
  set: action((state, payload) => {
    state.currency = payload
  }),
  add: action((state, {value, symbol, prefix}) => {
    state.currencies[value] = createCurrencySymbol({symbol, prefix})
    state.currency = value
  }),
  remove: action((state, payload) => {
    delete state.currencies[payload]

    if (state.currency === payload) {
      state.currency = head(keys(state.currencies))
    }
  }),
  modalOpened: false,
  openModal: action((state) => {
    state.modalOpened = true
  }),
  closeModal: action((state) => {
    state.modalOpened = false
  }),
}
