import styled from 'styled-components'

const autoRows = ({minRowHeight = '0px'}) => `minmax(${minRowHeight}, auto)`

const frGetter = value =>
  typeof value === 'number' ? `repeat(${value}, 1fr)` : value

const gap = ({gap = '0px'}) => gap

const flow = ({flow = 'row'}) => flow

const formatAreas = areas => areas.map(area => `"${area}"`).join(' ')

export const Grid = styled.div`
  display: grid;
  height: ${({height = 'auto'}) => height};
  grid-auto-flow: ${flow};
  grid-auto-rows: ${autoRows};
  ${({rows}) => rows && `grid-template-rows: ${frGetter(rows)}`};
  grid-template-columns: ${({columns = 2}) => frGetter(columns)};
  grid-gap: ${gap};
  ${({columnGap}) => columnGap && `column-gap: ${columnGap}`};
  ${({rowGap}) => rowGap && `row-gap: ${rowGap}`};
  ${({areas}) => areas && `grid-template-areas: ${formatAreas(areas)}`};
  ${({justifyContent}) =>
  justifyContent && `justify-content: ${justifyContent}`};
  ${({alignContent}) => alignContent && `align-content: ${alignContent}`};
`

export const Cell = styled.div`
  height: 100%;
  min-width: 0;
  grid-column-end: ${({ width = 1 }) => `span ${width}`};
  grid-row-end: ${({ height = 1 }) => `span ${height}`};
  ${({ left }) => left && `grid-column-start: ${left}`};
  ${({ top }) => top && `grid-row-start: ${top}`};
  ${({ center }) => center && `text-align: center`};
  ${({ area }) => area && `grid-area: ${area}`};
  ${({ middle }) => middle && `
    display: inline-flex;
    flex-flow: column wrap;
    justify-content: center;
    justify-self: stretch;
  `};
`
