import React, {useCallback} from 'react'
import {useStoreState, useStoreActions} from 'easy-peasy'
import styled from 'styled-components'
import {MdMenu} from 'react-icons/md'
import {openPrintWindow} from 'app/services/common'
import {Button} from 'app/components/Button'
import LogoIcon from 'app/static/logo.svg'
import {colors, metrics, noPrint, zIndex} from 'app/styles'
import {IconButton} from 'app/components/IconButton'

const componentColor = 'rgba(255, 255, 255, .9)'
const componentHoverColor = '#fff'

const Wrapper = styled.div`
  ${noPrint}
  height: ${metrics.headerHeight}px;
  line-height: ${metrics.headerHeight}px;
  padding: 0 32px;
  justify-content: space-between;
  display: flex;
  align-items: center;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: ${zIndex.header};
  background-color: ${colors.header};
`

const Logo = styled(LogoIcon)`
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  fill: ${componentColor};
  height: 24px;
  
  [theme='dark'] & {
    fill: ${colors.primary};
  }
`

const PrintButton = styled(Button)`
  margin-left: auto;
  color: ${componentColor};
  border: 1px solid ${componentColor};

  &:hover {
    color: ${componentHoverColor};
    border-color: ${componentHoverColor};
  }
`

const MenuButton = styled(IconButton)`
  font-size: 20px;
  color: ${componentColor};

  &:hover {
    color: ${componentHoverColor};
  }
`

export const Header = () => {
  const openSidebar = useStoreActions(actions => actions.settings.openSidebar)
  const closeSidebar = useStoreActions(actions => actions.settings.closeSidebar)
  const sidebarOpened = useStoreState(state => state.settings.sidebarOpened)
  const toggleSidebar = useCallback(() => sidebarOpened ? closeSidebar() : openSidebar(), [sidebarOpened])

  return (
    <Wrapper>
      <MenuButton Icon={MdMenu} onClick={toggleSidebar} />
      <Logo />
      <PrintButton
        onClick={openPrintWindow}
      >
        {'Print'}
      </PrintButton>
    </Wrapper>
  )
}
