import React from 'react'
import {useStoreActions, useStoreState} from 'easy-peasy'
import {Switch} from 'app/components/Switch'
import Row from './Row'

export default () => {
  const setDarkTheme = useStoreActions(actions => actions.settings.setDarkTheme)
  const darkTheme = useStoreState(state => state.settings.darkTheme)
  const onChange = e => setDarkTheme(e.target.checked)

  return (
    <Row label={'Dark theme'}>
      <Switch
        value={darkTheme}
        onChange={onChange}
      />
    </Row>
  )
}
