import {action, computed} from 'easy-peasy'
import {head, keys, path} from 'ramda'

export default () => ({
  companies: {},
  company: null,
  list: computed(state => keys(state.companies)),
  value: computed(state => path([state.company, 'value'])(state.companies)),
  set: action((state, payload) => {
    state.company = payload
  }),
  add: action((state, payload) => {
    state.companies[payload.label] = payload
    state.company = payload.label
  }),
  remove: action((state, payload) => {
    delete state.companies[payload]

    if (state.company === payload) {
      state.company = head(keys(state.companies))
    }
  }),
  modalOpened: false,
  openModal: action((state) => {
    state.modalOpened = true
  }),
  closeModal: action((state) => {
    state.modalOpened = false
  }),
})
