import React, {useCallback, useMemo} from 'react'
import {useStoreActions} from 'easy-peasy'
import {MdAdd, MdRemove} from 'react-icons/md'
import {OptionIcon, OptionWithIcon} from 'app/components/Select/Option'
import {useCompanyModel} from 'app/components/Company/context'
import {newCompany} from 'app/components/Company/constants'

export default ({value, children, ...props}) => {
  const model = useCompanyModel()
  const removeCompany = useStoreActions(actions => actions[model].remove)

  const remove = useCallback(e => {
    e.stopPropagation()
    removeCompany(value)
  }, [value])

  const icon = useMemo(() => {
    if (value === newCompany) {
      return (
        <OptionIcon
          Icon={MdAdd}
          title={'Add company'}
        />
      )
    }

    return (
      <OptionIcon
        Icon={MdRemove}
        title={'Remove company'}
        onClick={remove}
      />
    )
  }, [value])

  return (
    <OptionWithIcon {...props}>
      {children}
      {icon}
    </OptionWithIcon>
  )
}
