import React from 'react'
import styled from 'styled-components'
import {colors} from 'app/styles'
import {useInputNumber} from 'app/hooks'
import {inputStyle} from 'app/components/Input/constants'

const BaseInput = styled.input`
  ${inputStyle}
`

export const Input = ({inputRef, component: Component = BaseInput, ...props}) => (
  <Component ref={inputRef} {...props} />
)

export const InputNumber = props => {
  const {onChange, ...inputProps} = useInputNumber(props)

  const handleChange = e => onChange(e.target.value)

  return (
    <Input {...inputProps} onChange={handleChange}/>
  )
}

export const DashedInput = styled(Input)`
  padding: 0;
  height: 22px;
  border-radius: 0;
  border: 1px dotted ${colors.inputBorder};
  line-height: 20px;
  font-size: 12px;
  font-family: Arial, Helvetica, sans-serif;
  
  @media print {
    border: none;
  }
`

export const LabelInput = styled(DashedInput)`
  font-weight: 700;
`
