import {action} from 'easy-peasy'
import {DEFAULT_DATE_FORMAT, DEFAULT_PRIMARY_COLOR} from 'app/constants/common'
import {getBrowserDarkTheme} from 'app/services/common'

export default {
  tax: undefined,
  setTax: action((state, payload) => {
    state.tax = payload
  }),
  dateFormat: DEFAULT_DATE_FORMAT,
  setDateFormat: action((state, payload) => {
    state.dateFormat = payload
  }),
  darkTheme: getBrowserDarkTheme(),
  setDarkTheme: action((state, payload) => {
    state.darkTheme = payload
  }),
  primaryColor: DEFAULT_PRIMARY_COLOR,
  setPrimaryColor: action((state, payload) => {
    state.primaryColor = payload
  }),
  sidebarOpened: true,
  openSidebar: action((state) => {
    state.sidebarOpened = true
  }),
  closeSidebar: action((state) => {
    state.sidebarOpened = false
  }),
}
