import React from 'react'

export const INVOICE_ROUTE = 'invoice'

// The top-level (parent) route
const routes = {
  path: '',

  // Keep in mind, routes are evaluated in order
  children: [
    {
      path: '/',
      name: INVOICE_ROUTE,
      action: require('./invoice').default,
    },
    // Wildcard routes, e.g. { path: '*', ... } (must go last)
    {
      path: '(.*)',
      action: require('./notFound').default,
    },
  ],

  async action({next}) {
    // Execute each child route until one of them return the result
    const route = await next()

    // Provide default values for title, description etc.
    route.title = `${route.title || 'Invoice'}`
    route.description = route.description || 'A free and flexible invoice generator which stores all data only in your browser'

    return route
  },

}

// The error page is available by permanent url for development mode
if (__DEV__) {
  routes.children.unshift({
    path: '/error',
    action: require('./error').default,
  })
}

export default routes
