import React, {useCallback, useRef} from 'react'
import styled from 'styled-components'
import {prop} from 'ramda'
import ChromePicker from 'react-color/lib/Chrome'
import {useKeyPress, useOnClickOutside, useToggle} from 'app/hooks'
import {KEYS} from 'app/constants/common'
import {borderRadius, boxShadow, colors, zIndex} from 'app/styles'

const Container = styled.div`
  position: relative;
  width: 100%;
`
const Wrapper = styled.a`
  display: flex;
  background: ${colors.component};
  border: 1px solid ${colors.inputBorder};
  height: 24px;
  border-radius: ${borderRadius}px;
  padding: 4px;
`
const Color = styled.div`
  flex: 1;
  border-radius: ${borderRadius / 2}px;
  background-color: ${prop('color')};
`
const Popover = styled.div`
  position: absolute;
  left: -90px;
  bottom: -5px;
  z-index: ${zIndex.colorPicker};
`
const Picker = styled(ChromePicker)`
  position: inherit;
  border-radius: ${borderRadius}px !important;
  box-shadow: ${boxShadow.select} !important;
  background: ${colors.component} !important;

  & > div:first-child {
    border-radius: ${borderRadius}px ${borderRadius}px 0 0 !important;
  }
  
  & > div:last-child {
    border-radius: 0 0 ${borderRadius}px ${borderRadius}px;
    background-color: ${colors.component} !important;
  }

  & input {
    background-color: ${colors.component};
    color: ${colors.text} !important;
    box-shadow: none !important;
    border: 1px solid ${colors.inputBorder} !important;
  }

  & svg {
    fill: ${colors.icon} !important;
    background-color: transparent !important;
  }
`

export const ColorPicker = ({value, onChange}) => {
  const pickerRef = useRef()
  const [opened, {toggle, close}] = useToggle()
  const onSelect = useCallback((color) => onChange(color.hex, color), [])

  useKeyPress(KEYS.escape, close)

  useOnClickOutside(pickerRef, close)

  return (
    <Container ref={pickerRef}>
      <Wrapper onClick={toggle}>
        <Color
          color={value}
        />
      </Wrapper>
      {opened && (
        <Popover>
          <Picker
            color={value}
            onChange={onSelect}
            disableAlpha
          />
        </Popover>
      )}
    </Container>
  )
}
