import React from 'react'
import {useFormikContext} from 'formik'
import styled from 'styled-components'
import {createCurrencySymbol} from 'app/services/common'
import {borderRadius, colors} from 'app/styles'
import {useFormattedNumber} from 'app/hooks'

const Preview = styled.div`
  display: flex;
  justify-content: center;
  border: 1px solid ${colors.border};
  margin-bottom: 16px;
  padding: 16px;
  border-radius: ${borderRadius}px;
`

export default () => {
  const {values: {symbol, prefix}} = useFormikContext()

  const currencySymbol = createCurrencySymbol({symbol, prefix})

  const formattedNumber = useFormattedNumber(100, {currencySymbol})

  return (
    <Preview>{symbol ? formattedNumber : '[Preview]'}</Preview>
  )
}
