import React, {useMemo} from 'react'
import {Formik} from 'formik'
import {DEFAULT_ITEMS, LABELS} from 'app/constants/common'
import {useAppContext} from 'app/components/Layout/context'
import Form from 'app/components/Items/Form'

export const Items = () => {
  const {query} = useAppContext()

  const initialValues = useMemo(() => ({
    item: LABELS.item,
    quantity: LABELS.quantity,
    price: LABELS.price,
    amount: LABELS.amount,
    tax: LABELS.tax,
    total: LABELS.total,
    items: query.items || DEFAULT_ITEMS
  }), [query.items])

  return (
    <Formik
      component={Form}
      initialValues={initialValues}
    />
  )
}
