import React from 'react'
import {useStoreActions, useStoreState} from 'easy-peasy'
import {DEFAULT_DATE_FORMAT} from 'app/constants/common'
import {Input} from 'app/components/Input'
import Row from './Row'

export default () => {
  const setDateFormat = useStoreActions(actions => actions.settings.setDateFormat)
  const dateFormat = useStoreState(state => state.settings.dateFormat)
  const onChange = e => setDateFormat(e.target.value)

  return (
    <Row label={'Date format'}>
      <Input
        size='small'
        value={dateFormat}
        onChange={onChange}
        placeholder={DEFAULT_DATE_FORMAT}
      />
    </Row>
  )
}
