import React from 'react'
import styled from 'styled-components'
import {Cell, Grid} from 'app/components/Grid'
import {Field} from 'app/components/Field'
import AddButton from 'app/components/Details/AddButton'
import RemoveButton from 'app/components/Details/RemoveButton'
import {DashedInput, LabelInput} from 'app/components/Input'

const Row = styled(Grid)`
  position: relative;
`
const Input = styled(LabelInput)`
  text-align: right;
`

const Item = ({index, push, remove}) => (
  <Row gap='0px 16px'>
    <Cell>
      <Field
        as={Input}
        name={`details[${index}].label`}
      />
    </Cell>
    <Cell>
      <Field
        as={DashedInput}
        name={`details[${index}].value`}
      />
    </Cell>
    <AddButton index={index} push={push}/>
    <RemoveButton index={index} remove={remove}/>
  </Row>
)

export default ({push, remove, form: {values}}) =>
  values.details.map((item, index) =>
    <Item key={index} index={index} push={push} remove={remove}/>
  )

