import React from 'react'
import {useStoreActions, useStoreState} from 'easy-peasy'
import {useFormikContext} from 'formik'
import {Modal} from 'app/components/Modal'
import CompanyLabel from 'app/components/Company/CompanyLabel'
import {useCompanyModel} from 'app/components/Company/context'

export default () => {
  const model = useCompanyModel()
  const {handleSubmit} = useFormikContext()
  const close = useStoreActions(actions => actions[model].closeModal)
  const opened = useStoreState(state => state[model].modalOpened, [model])

  return (
    <Modal
      title={'Add Company'}
      opened={opened}
      onCancel={close}
      onOk={handleSubmit}
      width={400}
    >
      <CompanyLabel/>
    </Modal>
  )
}
