import React, {useEffect, useRef} from 'react'
import styled from 'styled-components'
import {MdKeyboardArrowDown} from 'react-icons/md'
import {useCounter, useKeyPress, useOnClickOutside, useToggle} from 'app/hooks'
import {KEYS} from 'app/constants/common'
import {IconButton} from 'app/components/IconButton'
import {borderRadius, boxShadow, colors, ellipsis, zIndex} from 'app/styles'
import {inputStyle} from 'app/components/Input/constants'
import {Option} from 'app/components/Select/Option'

const Container = styled.div`
  position: relative;
  max-width: 100%;
`
const ArrowButton = styled(IconButton)`
  position: absolute;
  right: 5px;
  transform: ${({opened}) => opened ? 'rotate(180deg)' : 'none'};
  transition: transform 0.3s;
`
const Input = styled.div`
  ${inputStyle}
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
  user-select: none;
  padding-right: 20px;
`
const Placeholder = styled.div`
  ${ellipsis}
`
const Options = styled.ul`
  position: absolute;
  right: 0;
  left: 0;
  background-color: ${colors.component};
  border-radius: ${borderRadius}px;
  overflow: hidden;
  margin-top: 2px;
  box-shadow: ${boxShadow.select};
  z-index: ${zIndex.select};
`

export const Select = ({style, className, value, Option, onChange, options}) => {
  const currentIndex = options.findIndex(item => item.value === value)
  const selectRef = useRef()
  const [cursor, {set, inc, dec}] = useCounter(currentIndex)
  const [opened, {toggle, close}] = useToggle()

  useOnClickOutside(selectRef, close)

  useEffect(() => {
    set(currentIndex)
  }, [value])

  useKeyPress(KEYS.escape, close)

  useKeyPress(KEYS.tab, () => {
    if (opened) {
      close()
    }
  })

  useKeyPress(KEYS.arrowUp, () => {
    if (opened && cursor > 0) {
      dec()
    }
  })

  useKeyPress(KEYS.arrowDown, () => {
    if (opened && cursor < options.length - 1) {
      inc()
    }
  })

  useKeyPress(KEYS.enter, () => {
    if (opened) {
      onChange(options[cursor].value)
      close()
    }
  })

  return (
    <Container ref={selectRef}>
      <Input style={style} className={className} onClick={toggle}>
        <Placeholder>{value}</Placeholder>
        <ArrowButton
          opened={opened}
          Icon={MdKeyboardArrowDown}
        />
      </Input>
      {opened && (
        <Options>
          {options.map((option, index) =>
            <Option
              key={option.value}
              value={option.value}
              onClick={() => {
                onChange(option.value)
                close()
              }}
              active={cursor === index}
              selected={option.value === value}
            >
              {option.label}
            </Option>
          )}
        </Options>
      )}
    </Container>
  )
}

Select.defaultOptions = {
  Option,
  options: [],
}
