import React from 'react'
import {useStoreActions, useStoreState} from 'easy-peasy'
import {Input} from 'app/components/Input'
import Row from './Row'

export default () => {
  const setTax = useStoreActions(actions => actions.settings.setTax)
  const tax = useStoreState(state => state.settings.tax)
  const onChange = e => setTax(e.target.value)

  return (
    <Row label={'Tax value'}>
      <Input
        size='small'
        placeholder={'Type % or text'}
        value={tax}
        onChange={onChange}
      />
    </Row>
  )
}
