import React, {useMemo} from 'react'
import {Formik} from 'formik'
import {LABELS} from 'app/constants/common'
import {useAppContext} from 'app/components/Layout/context'
import {Field} from 'app/components/Field'
import {LabelInput} from 'app/components/Input'

const Form = () => (
  <Field
    as={LabelInput}
    name='billTo'
  />
)

export const BillTo = () => {
  const {query} = useAppContext()

  const initialValues = useMemo(() => ({
    billTo: query.billTo || LABELS.billTo
  }), [query.billTo])

  return (
    <Formik
      component={Form}
      initialValues={initialValues}
    />
  )
}
