import React, {useCallback, useMemo} from 'react'
import {useStoreActions, useStoreState} from 'easy-peasy'
import {CurrencyModal} from 'app/components/CurrencyModal'
import {Select} from 'app/components/Select'
import {newCurrency} from './constants'
import CurrencyOption from './CurrencyOption'
import Row from './Row'

export default () => {
  const open = useStoreActions(actions => actions.currency.openModal)
  const setCurrency = useStoreActions(actions => actions.currency.set)
  const currency = useStoreState(state => state.currency.currency)
  const list = useStoreState(state => state.currency.list)

  const options = useMemo(() => [
    {value: newCurrency, label: 'Add currency'},
    ...list.map(value => ({
      value,
      label: value
    }))
  ], [list])

  const onChange = useCallback(value => {
    if (value === newCurrency) {
      open()
    } else {
      setCurrency(value)
    }
  })

  return (
    <Row label={'Currency'}>
      <Select
        value={currency}
        options={options}
        Option={CurrencyOption}
        onChange={onChange}
      />
      <CurrencyModal/>
    </Row>
  )
}
