import React from 'react'
import styled from 'styled-components'
import {MdAdd} from 'react-icons/md'
import {useFormikContext} from 'formik'
import {sizes} from 'app/styles'
import {DEFAULT_ITEM} from 'app/constants/common'
import Button from 'app/components/Details/Button'

const AddButton = styled(Button)`
  left: -32px;
  bottom: -${sizes.icon / 2}px;
`

export default ({index, push}) => {
  const {values} = useFormikContext()

  const onClick = () => push(DEFAULT_ITEM)

  if (index !== values.details.length - 1) {
    return null
  }

  return (
    <AddButton
      Icon={MdAdd}
      onClick={onClick}
    />
  )
}
