import React from 'react'
import {useStoreState} from 'easy-peasy'
import styled from 'styled-components'
import {Grid} from'app/components/Grid'
import {colors, metrics, noPrint, zIndex} from 'app/styles'
import TaxInput from 'app/components/Sidebar/TaxInput'
import DateFormat from 'app/components/Sidebar/DateFormat'
import CurrencySelect from 'app/components/Sidebar/CurrencySelect'
import ThemeSwitch from 'app/components/Sidebar/ThemeSwitch'
import ColorSelect from 'app/components/Sidebar/ColorSelect'
import Contact from 'app/components/Sidebar/Contact'
import DataManager from 'app/components/Sidebar/DataManager'

// TODO hide sidebar on finishing animation
const Drawer = styled.div`
  ${noPrint}
  width: ${metrics.sidebarWidth}px;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  background-color: ${colors.component};
  z-index: ${zIndex.sidebar};
  margin-top: 16px;
  padding-top: ${metrics.headerHeight}px;
  padding-right: 16px;
  padding-left: 16px;
  box-shadow: rgba(0, 0, 0, 0.15) 2px 0px 8px;
  transition: transform .3s cubic-bezier(.7, .3, .1, 1);
  transform: ${({visible}) => `translateX(${visible ? 0 : -100}%)`};
`

export const Sidebar = () => {
  const sidebarOpened = useStoreState(state => state.settings.sidebarOpened)

  return (
    <Drawer
      visible={sidebarOpened}
    >
      <Grid gap='24px 16px'>
        <TaxInput/>
        <DateFormat/>
        <CurrencySelect/>
        <ThemeSwitch/>
        <ColorSelect/>
        <DataManager/>
      </Grid>
      <Contact/>
    </Drawer>
  )
}
