import React, {useRef} from 'react'
import Modal from './Modal'
import ModalContext from './context'

export const ModalProvider = ({children}) => {
  const modalRef = useRef()

  return (
    <ModalContext.Provider value={{modalRef}}>
      {children}
      <div ref={modalRef} />
    </ModalContext.Provider>
  )
}

export { Modal }
