import {useFormikContext} from 'formik'
import {map, sum} from 'ramda'
import {useStoreState} from 'easy-peasy'
import {useFormattedNumber} from 'app/hooks'

export const getItemAmount = ({quantity, price}) => {
  if (isNaN(price) || isNaN(quantity)) return 0
  return price * quantity
}

const useSubTotalAmount = () => {
  const { values } = useFormikContext()
  return sum(map(getItemAmount)(values.items))
}

export const useTax = () => {
  const tax = useStoreState(state => state.settings.tax)
  const isNumber = !isNaN(+tax)
  const subTotal = useSubTotalAmount()
  const amount = isNumber ? subTotal * tax / 100 : 0
  const value = useFormattedNumber(amount)

  return {
    amount,
    visible: !!tax,
    value: isNumber ? value : tax
  }
}

export const useTotalValue = () => {
  const subTotal = useSubTotalAmount()
  const tax = useTax()
  return useFormattedNumber(subTotal + tax.amount)
}
