import React from 'react'
import {Formik} from 'formik'
import {useStoreActions} from 'easy-peasy'
import Modal from 'app/components/CurrencyModal/CurrencyModal'

const validate = (values) => {
  let errors = {}
  if (!values.value) {
    errors.value = 'Required'
  } else if (!values.symbol) {
    errors.symbol = 'Required'
  }
  return errors
}

const initialValues = {
  value: '',
  symbol: '',
  prefix: false,
}

export const CurrencyModal = () => {
  const addCurrency = useStoreActions(actions => actions.currency.add)

  const onSubmit = (values, {resetForm}) => {
    addCurrency(values)
    resetForm()
  }

  return (
    <Formik
      onSubmit={onSubmit}
      validate={validate}
      initialValues={initialValues}
      component={Modal}
    />
  )
}
