import {useLayoutEffect} from 'react'
import {setManifest} from 'app/services/common'
import {DARK_HEADER_BACKGROUND} from 'app/constants/common'

export const useManifestTheme = (darkTheme, primaryColor) => {
  useLayoutEffect(
    () => {
      setManifest(darkTheme ? {
        theme_color: DARK_HEADER_BACKGROUND,
        background_color: DARK_HEADER_BACKGROUND,
      } : {
        theme_color: primaryColor,
        background_color: primaryColor,
      })
    },
    [darkTheme, primaryColor]
  )
}
