import React from 'react'
import {Col} from 'app/components/Items/Row'
import {Field} from 'app/components/Field'
import {useTax} from 'app/components/Items/utils'
import Footer from 'app/components/Items/Footer'
import {LabelInput} from 'app/components/Input'

export default () => {
  const tax = useTax()

  if (!tax.visible) {
    return null
  }

  return (
    <Footer columns={4}>
      <Col left={3}>
        <Field
          as={LabelInput}
          name='tax'
        />
      </Col>
      <Col>{tax.value}</Col>
    </Footer>
  )
}
