import React, {useMemo} from 'react'
import styled from 'styled-components'
import {Formik} from 'formik'
import TinyMCE from 'app/components/TinyMCE'
import {LABELS, TINYMCE_CONFIG} from 'app/constants/common'
import {useAppContext} from 'app/components/Layout/context'
import {Field} from 'app/components/Field'

const Input = styled(TinyMCE)`
  margin-top: 48px;

  .mce-content-body {
    min-height: 80px;
  }
`

const Form = () => (
  <Field
    as={Input}
    tinymceConfig={TINYMCE_CONFIG}
    name='notes'
  />
)


export const Notes = () => {
  const {query} = useAppContext()

  const initialValues = useMemo(() => ({
    notes: query.notes || LABELS.notes
  }), [query.notes])

  return (
    <Formik
      component={Form}
      initialValues={initialValues}
    />
  )
}
