import React, {useEffect, useState} from 'react'
import {both, complement, is} from 'ramda'
import {KEYS} from 'app/constants/common'

const isNumber = both(is(Number), complement(isNaN))
const isString = is(String)

// const IS_IOS =
//   typeof navigator !== 'undefined'
//     ? navigator.userAgent.match(/iPhone|iPad|iPod/i)
//     : false

export const useInputNumber = (
  {
    step = 1,
    min,
    max,
    value = '',
    onChange,
    ...props
  }
) => {
  const [text, setText] = useState(value)

  useEffect(() => {
    setText(value)
  }, [value])

  const handleChange = text => {
    const value = parseText(text)

    setText(text)
    onChange(value)
  }

  const handleWheel = e => {
    e.target.blur()
  }

  const handleKeyDown = e => {
    if (e.key === KEYS.arrowUp) {
      up()
    }
    if (e.key === KEYS.arrowDown) {
      down()
    }
  }

  const up = () => {
    onChange(changeValue('+', value, max, min, step))
  }

  const down = () => {
    onChange(changeValue('-', value, max, min, step))
  }

  const inputProps = {
    value: text,
    onChange: handleChange,
    onKeyDown: handleKeyDown,
    onWheel: handleWheel
  }

  // if (enableMobileNumericKeyboard) {
  //   return {
  //     ...props,
  //     ...inputProps,
  //     autoComplete: 'off',
  //     type: 'number',
  //     inputMode: 'numeric',
  //     pattern: IS_IOS ? '[0-9]*' : '',
  //     step,
  //     min,
  //     max
  //   }
  // }

  return {
    ...props,
    ...inputProps,
    autoComplete: 'off',
    type: 'text'
  }
}

const parseText = text => {
  if (isNumber(text)) return text

  if (isString(text)) {
    text = text.trim()

    if (!text) return ''
    const num = parseFloat(text)

    if (!isNaN(num)) {
      return num
    }
  }

  return ''
}

const changeValue = (mod, value, max, min, step) => {
  if (value === '') {
    if (isNumber(min)) return min
    return ''
  }

  value = mod === '+' ? value + step : value - step

  if (isNumber(max) && value > max) return max
  if (isNumber(min) && value < min) return min

  console.log(value)

  const p = (step.toString().split('.')[1] || []).length
  if (p) {
    return parseFloat(value.toFixed(p))
  }

  return value
}
