import React, {useCallback, useMemo} from 'react'
import {Formik} from 'formik'
import {useStoreActions, useStoreState} from 'easy-peasy'
import {CompanyContext} from 'app/components/Company/context'
import CompanyForm from 'app/components/Company/CompanyForm'

const validate = (values) => {
  let errors = {}
  if (!values.label) {
    errors.label = 'Required'
  }
  return errors
}

export const Company = ({initialValue, model}) => {
  const addCompany = useStoreActions(actions => actions[model].add)
  const value = useStoreState(state => state[model].value, [model])

  const onSubmit = useCallback((values, {resetForm}) => {
    addCompany(values)
    resetForm({value: values.value, label: ''})
  })

  const initialValues = useMemo(() => ({
    value: value || initialValue,
    label: ''
  }), [value, initialValue])

  return (
    <CompanyContext.Provider value={model}>
      <Formik
        validate={validate}
        onSubmit={onSubmit}
        enableReinitialize
        initialValues={initialValues}
        component={CompanyForm}
      />
    </CompanyContext.Provider>
  )
}
