import React, {useCallback, useContext, useRef} from 'react'
import ReactDOM from 'react-dom'
import styled from 'styled-components'
import {MdClose} from 'react-icons/md'
import {KEYS} from 'app/constants/common'
import {useKeyPress} from 'app/hooks'
import ModalContext from './context'
import {IconButton} from 'app/components/IconButton'
import {Button} from 'app/components/Button'
import {borderRadius, colors, boxShadow, zIndex} from 'app/styles'

const Mask = styled.div`
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: ${zIndex.modal};
  background-color: rgba(0, 0, 0, 0.5);
  align-items: center;
  justify-content: center;
`
// TODO avoid appearing width prop on div
const Wrapper = styled.div`
  width: ${({width = 500}) => `${width}px`};
  min-height: 300px;
  background-color: ${colors.component};
  border-radius: ${borderRadius}px;
  box-shadow: ${boxShadow.modal};
  display: flex;
  flex-direction: column;
`
const Header = styled.div`
  display: flex;
  align-items: center;
  position: relative;
  padding: 16px 24px;
  border-bottom: 1px solid ${colors.border};
`
const CloseButton = styled(IconButton)`
  position: absolute;
  right: 16px;
  font-size: 24px;
`
const Content = styled.div`
  padding: 24px;
  flex: 1;
`
const Footer = styled.div`
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 16px 24px;
  border-top: 1px solid ${colors.border};
  
  > * {
    margin-left: 16px;
  }
`

const Modal = ({width, children, title, opened, onCancel, onOk}) => {
  const {modalRef} = useContext(ModalContext)
  const maskRef = useRef()

  useKeyPress(KEYS.escape, onCancel)

  const onMaskPress = useCallback((e) => {
    if (e.target === maskRef.current) {
      onCancel()
    }
  }, [])

  if (!modalRef.current || !opened) {
    return null
  }

  return ReactDOM.createPortal((
    <Mask
      ref={maskRef}
      onClick={onMaskPress}
    >
      <Wrapper width={width}>
        <Header>
          {title}
          <CloseButton Icon={MdClose} onClick={onCancel}/>
        </Header>
        <Content>
          {children}
        </Content>
        <Footer>
          <Button onClick={onCancel}>{'Cancel'}</Button>
          <Button
            type={Button.type.primary}
            onClick={() => {
              onOk()
              onCancel()
            }}
          >
            {'OK'}
          </Button>
        </Footer>
      </Wrapper>
    </Mask>
  ), modalRef.current)
}

export default Modal
