import {useEffect} from 'react'

export const useKeyPress = (targetKey, callback) => {
  const handleEvent = ({key}) => {
    if (key === targetKey) {
      callback()
    }
  }

  useEffect(() => {
    window.addEventListener('keydown', handleEvent)
    return () => {
      window.removeEventListener('keydown', handleEvent)
    }
  }, [callback])
}
